padding-right: .5em;
}

   .bodyNoHeader {
width: 300px;
display: table;
padding-left: .5em;
padding-right: .5em;
}

   .emojiRight {
float: right;
color: #FFFFFF;
margin: 0 0 0.5em;
border-radius: 1em;
padding: 0.5em 1em;
max-width: 75%;
clear: both;
position: relative;
}

   .emojiLeft {
float: left;
color: #FFFFFF;
margin: 0 0 0.5em;
border-radius: 1em;
padding: 0.5em 1em;
max-width: 75%;
clear: both;
position: relative;
}

   .blueText {
float: right;
color: #FFFFFF;
margin: 0 0 0.5em;
border-radius: 1em;
padding: 0.5em 1em;
background: #1289fe;
max-width: 75%;
clear: both;
position: relative;
}

   .blueTextTail {
float: right;
color: #FFFFFF;
margin: 0 0 0.5em;
border-radius: 1em;
padding: 0.5em 1em;
background: #1289fe;
max-width: 75%;
clear: both;
position: relative;
}

   .blueTextTail::after {
content: "";
position: absolute;
right: -0.5em;
bottom: 0;
width: 0.5em;
height: 1em;
border-left: 0.5em solid #1289fe;
border-bottom-left-radius: 1em 0.5em;
}

   .greenText {
float: right;
color: #FFFFFF;
margin: 0 0 0.5em;
border-radius: 1em;
padding: 0.5em 1em;
background: #35cb24;
max-width: 75%;
clear: both;
position: relative;
}

   .greenTextTail {
float: right;
color: #FFFFFF;
margin: 0 0 0.5em;
border-radius: 1em;
padding: 0.5em 1em;
background: #35cb24;
max-width: 75%;
clear: both;
position: relative;
}

   .greenTextTail::after {
content: "";
position: absolute;
right: -0.5em;
bottom: 0;
width: 0.5em;
height: 1em;
border-left: 0.5em solid #35cb24;
border-bottom-left-radius: 1em 0.5em;
}

   .greyText {
float: left;
color: #000000;
margin: 0 0 0.5em;
border-radius: 1em;
padding: 0.5em 1em;
background: #e5e5ea;
max-width: 75%;
clear: both;
position: relative;
}

   .greyTextTail {
float: left;
color: #000000;
margin: 0 0 0.5em;
border-radius: 1em;
padding: 0.5em 1em;
background: #e5e5ea;
max-width: 75%;
clear: both;
position: relative;
}

   .greyTextTail::after {
content: "";
position: absolute;
left: -.5em;
bottom: 0;
width: 0.5em;
height: 1em;
border-right: 0.5em solid #e5e5ea;
border-bottom-right-radius: 1em 0.5em;
}

   .time {
min-width: 295px;
color: #7B7C80;
font-size: .75em;
padding-bottom: .2em;
padding-top: .2em;
margin-left: -.5em;
margin-right: -.5em;
margin-bottom: -.5em;
text-align: center;
display: table;
}

   .typing-indicator {
background-color: #e6e7ed;
width: auto;
border-radius: 1em;
padding: .5em 1em;
display: table;
margin: 0 0 .5em;
position: relative;
overflow: hidden;
text-align: left;
clear: both;
}

   .typing-indicator::before,
   .typing-indicator::after {
content: '';
position: absolute;
bottom: -2px;
left: -2px;
height: 12px;
width: 12px;
border-radius: 50%;
background-color: #e6e7ed;
}

   .typing-indicator::after {
height: 6px;
width: 6px;
left: -6px;
bottom: -6px;
}

   .typing-indicator span {
height: 15px;
width: 15px;
float: left;
margin: 0 1px;
background-color: #9E9EA1;
display: block;
border-radius: 50%;
opacity: 0.4;
}

   .typing-indicator span {
height: .65em;
width: .65em;
float: left;
margin: .425em 0 .425em .25em;
margin-left: 0.25em;
background-color: #9e9ea1;
display: inline-block;
border-radius: 50%;
opacity: .5;
}

   .typing-indicator span:first-child {
margin-left: 0;
}

   .typing-indicator span:nth-child(1) {
opacity: .85;
}

   .typing-indicator span:nth-child(2) {
opacity: .65;
}

.grouptext {
color: #7B7C80;
font-size: .75em;
padding-bottom: .5em;
padding-top: 0;
margin-left: .5em;
margin-bottom: -2.5em;
text-align: left;
display: table;
clear: both;
}

.readreceipt {
color: #7B7C80;
font-size: .75em;
float: right;
position: relative;
clear: both;
margin: -.75em -1.25em 0.5em 0;
padding: 0 1em 0.5em;
}